/*SCREEN PC*/
.navbar {
  font-family: "Segoe UI", "Segoe UI Web (West European)", "Segoe UI", -apple-system, BlinkMacSystemFont, Roboto, "Helvetica Neue", sans-serif;
  font-size: 14px;
  background-color: #c0c1c5;
  color: black;
}
.navbar .feature {
  display: none;
}
.navbar .navbar-nav {
  padding-left: 24px !important;
  display: flex;
  flex-wrap: wrap;
}
.navbar .navbar-nav .nav-link {
  margin-right: 16px;
  padding: 0 4px !important;
}
.navbar .navbar-nav .nav-link span {
  margin: 0 4px;
}
.navbar button {
  margin-left: 80%;
}

#btnCreate {
  float: right;
}

#createContent {
  display: none;
}
#createContent label {
  margin-bottom: 5px;
  margin-top: 5px;
}

.dropdown-content {
  display: none;
  outline: none;
  max-height: 830px;
  position: relative;
  border-radius: 4px;
  background: white;
  box-sizing: border-box;
  font-size: 14px;
  width: 160px;
  padding-left: 44px;
  box-shadow: rgba(0, 0, 0, 0.11) 0px 1.2px 3.6px, rgba(0, 0, 0, 0.13) 0px 6.4px 14.4px;
  margin-left: 37px;
  margin-bottom: 0px;
  padding-left: 10px;
  z-index: 1;
}
.dropdown-content .content {
  padding-left: 0px;
}
.dropdown-content .content li {
  list-style-type: none;
  height: 32px;
}
.dropdown-content .content li button {
  background-color: transparent;
  border: none;
  width: 100%;
  height: 32px;
  line-height: 32px;
  display: block;
  cursor: pointer;
  text-align: left;
  padding-left: 0px;
}
.dropdown-content .content li button span {
  margin-left: 6px;
}

.nav_input2:checked ~ .dropdown-content {
  display: block;
  z-index: 1;
}

/*---NAV-MOBILE*/
.nav_mobile {
  position: fixed;
  top: 56px;
  right: 0;
  width: 100%;
  bottom: 61%;
  background-color: #ececec;
  transform: translateX(100%);
  opacity: 0;
  transition: transform linear 0.2s, opacity linear 0.2s;
}
.nav_mobile .nav_mobile-list {
  margin-bottom: 0px;
  padding-left: 0px;
  display: flex;
  flex-direction: column;
}
.nav_mobile .nav_mobile-list .nav_mobile-link {
  font-family: "Segoe UI", "Segoe UI Web (West European)", "Segoe UI", -apple-system, BlinkMacSystemFont, Roboto, "Helvetica Neue", sans-serif;
  font-size: 14px;
  color: rgba(0, 0, 0, 0.9);
  display: block;
  padding: 8px 0;
  padding-left: 20px;
}
.nav_mobile .nav_mobile-list .nav_mobile-link i {
  width: 14px;
  height: 14px;
}
.nav_mobile .nav_mobile-list .nav_mobile-link:hover {
  background-color: #8f8d8d;
}

/*DROP DOWN*/
.nav_input:checked ~ .nav_mobile {
  transform: translateX(0%);
  opacity: 1;
}

@media (max-width: 768px) {
  .navbar {
    display: none;
  }

  .nav_mobile {
    z-index: 1;
  }
}
.aside-pc {
