}
.aside-pc .container-fluid {
  margin-left: 73px;
  margin-right: 1px !important;
}
.aside-pc .container-fluid .row {
  padding: 8px 8px;
  color: black;
}
.aside-pc .container-fluid .row .icon {
  width: 40px !important;
}
.aside-pc .container-fluid .row .name {
  width: 370px !important;
}
.aside-pc .container-fluid .row .space {
