.aside-pc .container-fluid #row-2 .modify span, .aside-pc .container-fluid #row-6 .modify span, .aside-pc .container-fluid #row-5 .modify span, .aside-pc .container-fluid #row-4 .modify span, .aside-pc .container-fluid #row-3 .modify span {
